@import './global-style';

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html,
input,
button {
	font-family: 'Nunito Sans', sans-serif;
}

@media screen and (max-width: $width-mobile) {
	html {
		font-size: 13px;
	}
}

@media screen and (max-width: $width-laptop) {
	html {
		font-size: 14px;
	}
}
