$dark-mode-elements: hsl(209, 23%, 22%);
$dark-mode-background: hsl(207, 26%, 17%);
$light-mode-text: hsl(200, 15%, 8%);
$light-mode-input: hsl(0, 0%, 52%);
$light-mode-background: hsl(0, 0%, 98%);
$light-mode-elements: hsl(0, 0%, 100%);

$width: 87vw;
$width-mobile: 500px;
$width-laptop: 1024px;

$border-radius: 5px;

@mixin box-shadow {
	box-shadow: 0px 4px 8px -6px rgba(0, 0, 0, 0.42);
}

@mixin box-shadow-hover {
	box-shadow: 0px 7px 8px -2px rgba(0, 0, 0, 0.42);
}

.light {
	background-color: white;
	color: black;
	transition: all 0.3s;
}

.dark {
	background-color: $dark-mode-elements;
	color: $light-mode-elements;
	transition: all 0.3s;
}

.light-background{
	background-color: $light-mode-background;
}

.dark-background{
	background-color: $dark-mode-background;
}

//link style reset
.link {
	text-decoration: none;

	&:focus,
	&:hover,
	&:visited,
	&:link,
	&:active {
		text-decoration: none;
	}
}
